@import "~slick-carousel/slick/slick.css"; 
@import "~slick-carousel/slick/slick-theme.css";

.dashborad__landing {
  position: relative;
}

.dashborad__hero {
  height: 100vh;
  position: relative;
}

.dashborad__hero-image-wrapper {
  height: 100%;
  position: relative;
}

.dashborad__hero-img-gradient {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, .4);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, .8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, .8) 100%
  );
}

.dashborad__hero-content-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: white;
  max-width: 36em;
  margin: 0 2.5em;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 1em;
}

.dashborad__welcome-text {
  font-size: 1.75rem;
  font-weight: 600;
}

.dashborad__hero-title {
  font-size: 3.25rem;
  margin: 0.25em 0;
}

.dashboard__overview {
  line-height: 1.4rem;
  font-size: .95rem;
}

.dashborad__button {
  background-color: #29292980;
  color: white;
  border: 0;
  cursor: pointer;
  padding: .85rem 2.25rem;
  font-size: 1.25rem;
  transition: 0.2s;
  border-radius: 1px;
  margin-top: 1em;
}

.dashborad__button:hover {
  transform: translateY(-2px);
  opacity: 0.9;
}

.dashborad__header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
}

.dashborad__logo {
  padding: 0.5rem 2rem;
}

.dashborad__logo > img {
  max-width: 13em;
}

.row__title {
    font-size: 1.25rem;
    margin: 2rem 0 1rem 0;
    font-weight: 600;
}

@media screen and (max-width: 767px) {
  .dashborad__welcome-text {
    font-size: 1.45rem;
    font-weight: 600;
  }

  .dashborad__hero-title {
    font-size: 2.85rem;
  }

  .dashborad__button {
    padding: .8rem 2.85rem;
    font-size: 1.25rem;
  }
}

@media screen and (max-width: 500px) {
  .dashboard__overview {
    line-height: 1.4em;
    height: 5em;
    overflow: hidden;
    white-space: pre-wrap;
    text-overflow: ellipsis;
  }
  .dashborad__hero-content-wrapper {
    margin: 0 1em;
  }
  .dashborad__welcome-text {
    font-size: 1rem;
    font-weight: 600;
  }

  .dashborad__hero-title {
    font-size: 1.85rem;
  }

  .dashborad__button {
    padding: .6rem 2.25rem;
    font-size: 1rem;
  }
}
